.spinner-container {
    align-items: center;
    background-color: var(--primario);
    display: flex;
    justify-content: center;
    margin: 0px;
    height: 100vh;
    width: 100vw;
}

.spinner {
    border: 4px solid rgba(0, 0, 0, .1);
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border-left-color: #fff;

    animation: spin 1s linear infinite;
}

@keyframes spin {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}